.nochlejka {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/nochlejka/nochlejka.png');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/nochlejka/mobile_nochlejkaj.jpg');
    }
    .card_block {
        .card_text {
            align-self: flex-start;
            margin: 10% 0 0 5%;
            .card_title {
                max-width: 733px;
            }
        }
        .card_dialog {
            grid-template-columns: 1fr 3fr;
        }
    }
}