.kvartira {
    overflow-x: hidden;
    position: relative;
    width: 100vw;
    height: 100vh;
    @include bgPosition('../img/kvartira/radji.webp');
    @media screen and (max-width: 800px) {
        @include bgPosition('../img/kvartira/radjiMobile.webp');
    }
    .card_block {
        .card_text {
            align-self: flex-end;
            margin: 10% 5% 0 0;
        }
        .card_item-desctop {
            position: fixed;
            width: 164.56px;
            height: 164.56px;
            left: 4%;
            bottom: 15%;
            @media (max-width: 800px) {
                display: none;
            }
        }
        .card_mobile-img {
            position: relative;
            .card_item-mobile {
                display: none;
                @media (max-width: 800px) {
                    display: block;
                    position: absolute;
                    max-width: 17%;
                    top: 58%;
                    left: 27%;
                }
            }
            
        }
        .card_dialog {
            display: none;
            max-width: 827px;
            grid-template-columns: 1fr;
            .card_dialog-text {
                justify-self: center;
            }
            @media (max-width: 850px) {
                width: 60vw;
            }
            .map_link {
                font-family: 'SangBleuSunrise';
                text-transform: uppercase;
                text-decoration: none;
                display: flex;
                justify-content: center;
                align-items: start;
                margin: 0;
                .remove_map {
                    color: #B58260;
                    font-size: 28px;
                    @media (max-width: 800px) {
                        // margin: 0 auto;
                        text-align: center;
                        font-size: 18px;
                        line-height: 24px;
                    }
                }
            }
            
        }
        .card_label {
            margin: 0% auto 0% auto;
        }
    }
